:root{
  --green: #00aa00;
  --yellow: yellow;
  --offYellow: #c9c900;
  --white: white;
  --gray: #5f5f5f;
}

body{
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.board{
  height: 100vh;
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  overflow: auto;
  position: absolute;
  background-image: url("./assets/split-wall.jpg");
  background-size: cover;
  background-repeat: no-repeat;
}

.note{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-direction: column;
      flex-direction: column;
  position: relative;
  width: 150px;
  height: 150px;
  margin: 10px;
  padding: 5px;
  background-color: var(--yellow);
}

.btn-row{
  position: absolute;
  bottom: 5px;
  right: 5px;
}

button{
  margin: 0 5px;
  padding: 3px 5px;
  border: none;
  border-radius: 5px;
  color: var(--white);
}

button:hover{
  cursor: pointer;
}

button:focus{
  outline: none;
}

#save{
  background-color: var(--green);
}

#edit{
  background-color: var(--gray);
}

#edit-form{
  width: 140px;
  height: 110px;
  margin-top: -25px;  background-color: var(--yellow);
  border: 1px solid var(--offYellow);
}

#check{
  background-color: var(--green);
}

#remove{
  background-color: red;
}


